import React from 'react'
import './style.css'

function Input({ type = 'text', label = '', placeholder, ...otherProps}) {
  let strLabel = label
  strLabel = strLabel.replace(/\s+/g, '-').toLowerCase()

  return (
    <div className="tur-wraper-input">
      { label ? <label htmlFor={strLabel}>{label}</label> : null }
      <input type={type} id={strLabel} placeholder={placeholder} {...otherProps}/>
    </div>
  )
}

export default Input
