import React from 'react'
import { Link } from 'react-router-dom'
import './style.css'

function Button({ type, href, ...props}) {
  let btnType = type === 'outline' ? 'tur-button--outline' : null
  if( href ) {
    return (
      <Link className={`tur-button ${btnType}`} to={href}>
        {props.children}
      </Link>
    )
  }else {
    return (
      <button className={`tur-button ${btnType}`}>
        {props.children}
      </button>
    )
  }
}

export default Button
