import React from 'react'
import { site_url } from '../../../config'
import { App, Header, Body, Footer } from '../../layouts' 
import { Button } from '../../elements' 
import './style.css'

function Transaction(props) {
  const id = props.match.params.id
  const title = id !== 1 ? 'Uang Keluar' : 'Uang Masuk'

  return (
    <App>
      <Header back={true} title={title} />
      <Body>

        <div className="tur-transaction-header">
          <p>Jumlah</p>
          <input type="number" defaultValue="50.000" />
        </div>


        <label htmlFor="Catatan">Catatan
          <select name="catatan" id="catatan">
            <option value="1">Bayar Angsuran MUR</option>
            <option value="1">Setor MUR</option>
          </select>
        </label>

        <div className="tur-transaction-action">
          <label htmlFor="photo">
            Photo
            <input id="photo" name="photo" type="file" />
            <i className="icon-camera"></i>
          </label>
          <label htmlFor="date">
            Tanggal Bayar
            <input type="date"/>
          </label>
        </div>

      </Body>
      <Footer>
        <div className="tur-col">
          <Button href={site_url.dashboard}>Simpan</Button>
        </div>
      </Footer>
    </App>
  )
}

export default Transaction
